import { useCallback } from '@wordpress/element';
import { MediaUpload, RichText } from '@wordpress/block-editor';
import { Button, TextareaControl, SelectControl, ToggleControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

export function useAttributeList(attributes, setAttributes, key) {
  const list = attributes[key] || [];

  const setList = useCallback(
    (newList) => setAttributes({ [key]: newList }),
    [key, setAttributes]
  );

  const add = useCallback(
    (itemTemplate = {}) => setList([...list, { ...itemTemplate }]),
    [list, setList]
  );

  const remove = useCallback(
    (index) => setList(list.filter((_, i) => i !== index)),
    [list, setList]
  );

  const update = useCallback(
    (index, field, value) =>
      setList(list.map((item, i) => (i === index ? { ...item, [field]: value } : item))),
    [list, setList]
  );

  const moveUp = useCallback(
    (index) => {
      if (index === 0) return;
      const newList = [...list];
      [newList[index - 1], newList[index]] = [newList[index], newList[index - 1]];
      setList(newList);
    },
    [list, setList]
  );

  const moveDown = useCallback(
    (index) => {
      if (index === list.length - 1) return;
      const newList = [...list];
      [newList[index], newList[index + 1]] = [newList[index + 1], newList[index]];
      setList(newList);
    },
    [list, setList]
  );

  // ----------------------------
  //    SelectTextareaControl
  // ----------------------------
  const renderSelectTextareaControl = (item, index) => {
    const imageName = `imageName`;
    const link = `link`;

    return (
      <>
        <SelectControl
          value={item[imageName] || ''}
          options={[
            { label: ' ', value: '' },
            { label: 'Telegram', value: 'telegram' },
            { label: 'Vk', value: 'vk' },
            { label: 'Youtube', value: 'youtube' },
            { label: 'Whatsapp', value: 'whatsapp' }
          ]}
          onChange={(value) => update(index, imageName, value)}
        />
        <TextareaControl
          placeholder={__('...', 'theme')}
          value={item[link]}
          onChange={(value) => update(index, link, value)}
          rows={1}
        />
      </>
    )
  };

  // ----------------------------
  //    TextareaToTextarea
  // ----------------------------
  const renderTextareaToTextarea = (item, index) => {
    const text = `text`;
    const link = `link`;

    return (
      <>
        <TextareaControl
          placeholder={__('...', 'theme')}
          value={item[text]}
          onChange={(value) => update(index, text, value)}
          rows={5}
        />
        <TextareaControl
          placeholder={__('...', 'theme')}
          value={item[link]}
          onChange={(value) => update(index, link, value)}
          rows={1}
        />
      </>
    )
  };

  // ----------------------------
  //    TextareaToTextarea
  // ----------------------------
  const renderRichTextToRichText = (item, index) => {
    const label = `label`;
    const content = `content`;

    return (
      <>
        <RichText
          placeholder={__('', 'theme')}
          value={item[label]}
          onChange={(value) => update(index, label, value)}
        />
        <RichText
          placeholder={__('  email', 'theme')}
          value={item[content]}
          onChange={(value) => update(index, content, value)}
        />
      </>
    )
  };

  // ----------------------------
  //    breadcrumbs
  // ----------------------------
  const renderBreadcrumbs = (item, index) => {
    const text = `text`;
    const hash = `hash`;

    return (
      <>
        <TextareaControl
          placeholder={__('...', 'theme')}
          value={item[text]}
          onChange={(value) => update(index, text, value)}
          rows={1}
        />
        <TextareaControl
          placeholder={__('', 'theme')}
          value={item[hash]}
          onChange={(value) => update(index, hash, value)}
          rows={1}
        />
      </>
    )
  };

  // ----------------------------
  //    blockFourteen
  // ----------------------------
  const renderBlockFourteen = (item, index) => {
    const image = `image`;
    const content = `content`;

    return (
      <>
        <MediaUpload
          onSelect={(media) =>
            update(index, image, {
              id: media.id,
              url: media.url,
              alt: media.alt || '',
            })
          }
          allowedTypes={['image']}
          value={item[image]?.id}
          render={({ open }) => (
            <div className="repeater-image" style={{ display: 'block', width: '100%' }}>
              {item[image]?.url ? (
                <div className="repeater-image-preview">
                  <img
                    src={item[image].url}
                    alt={item[image].alt || ''}
                    style={{
                      aspectRatio: '16 / 9',
                      width: '100%',
                      height: 'inherit',
                      marginBottom: '8px',
                      objectFit: 'cover',
                      borderRadius: '3px',
                    }}
                  />
                  <div className="repeater-image-controls" style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center', alignItems: 'stretch', width: '100%' }}>
                    <Button onClick={open} variant="secondary" size="small">
                       {__('', 'theme')}
                    </Button>
                    <Button
                      isDestructive
                      onClick={() => update(index, image, { id: 0, url: '', alt: '' })}
                      variant="secondary"
                      size="small"
                    >
                       {__('', 'theme')}
                    </Button>
                  </div>
                </div>
              ) : (
                <Button onClick={open} variant="primary" size="small">
                  {__(' ', 'theme')}
                </Button>
              )}
            </div>
          )}
        />
        <RichText
          style={{ textAlign: 'left' }}
          tagName="div"
          placeholder={__('...', 'theme')}
          value={item[content]}
          onChange={(value) => update(index, content, value)}
        />
      </>
    )
  };

  // ----------------------------
  //    TextareaToRichText
  // ----------------------------
  const renderTextareaToRichText = (item, index) => {
    const title = `title`;
    const content = `content`;

    return (
      <>
        <TextareaControl
          placeholder={__('', 'theme')}
          value={item[title]}
          onChange={(value) => update(index, title, value)}
          rows={1}
        />
        <RichText
          placeholder={__('', 'theme')}
          value={item[content]}
          onChange={(value) => update(index, content, value)}
        />
      </>
    )
  };

  // ----------------------------
  //    TextRichToRich
  // ----------------------------
  const renderTextRichToRich = (item, index) => {
    const num = `num`;
    const title = `title`;
    const content = `content`;

    return (
      <div style={{ display: 'flex', flex: 'wrap', justifyContent: 'space-between', alignItems: 'stretch', gap: '16px', width: '100%' }}>
        <div style={{ width: '30%' }}>
          <TextareaControl
            style={{ textAlign: 'left' }}
            placeholder={__('', 'theme')}
            value={item[num]}
            onChange={(value) => update(index, num, value)}
            rows={1}
          />
          <RichText
            style={{ textAlign: 'left' }}
            placeholder={__('', 'theme')}
            value={item[title]}
            onChange={(value) => update(index, title, value)}
          />
        </div>

        <div style={{
          width: 'calc(70% - 18px)',
          textAlign: 'left',
          minHeight: '100px'
        }}>
          <RichText
            style={{ textAlign: 'left' }}
            placeholder={__('', 'theme')}
            value={item[content]}
            onChange={(value) => update(index, content, value)}
          />
        </div>
      </div>
    )
  };

  // ----------------------------
  //    blockSeventeen
  // ----------------------------
  const renderBlockSeventeen = (item, index) => {
    const image = `image`;

    return (
      <>
        <MediaUpload
          onSelect={(media) =>
            update(index, image, {
              url: media.url,
              alt: media.alt || '',
              responsive: media.responsive || {
                webp: '',
                jpg: '',
                default: media.url,
              }
            })
          }
          allowedTypes={['image']}
          value={item[image]?.id}
          render={({ open }) => (
            <div className="repeater-image" style={{ display: 'block', width: '100%' }}>
              {item[image]?.url ? (
                <div className="repeater-image-preview">
                  <img
                    src={item[image].url}
                    alt={item[image].alt || ''}
                    style={{
                      aspectRatio: '16 / 9',
                      width: '100%',
                      height: 'inherit',
                      marginBottom: '8px',
                      objectFit: 'cover',
                      borderRadius: '3px',
                    }}
                  />
                  <div className="repeater-image-controls" style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center', alignItems: 'stretch', width: '100%' }}>
                    <Button onClick={open} variant="secondary" size="small">
                       {__('', 'theme')}
                    </Button>
                    <Button
                      isDestructive
                      onClick={() => update(index, image, { id: 0, url: '', alt: '' })}
                      variant="secondary"
                      size="small"
                    >
                       {__('', 'theme')}
                    </Button>
                  </div>
                </div>
              ) : (
                <Button onClick={open} variant="primary" size="small">
                  {__(' ', 'theme')}
                </Button>
              )}
            </div>
          )}
        />
      </>
    )
  };


  // ----------------------------
  //    blockSeventeen
  // ----------------------------
  const renderBlockNineteen = (item, index) => {
    const image = `image`;
    const content = `content`;

    return (
      <>
        <MediaUpload
          onSelect={(media) =>
            update(index, image, {
              url: media.url,
              alt: media.alt || '',
              responsive: media.responsive || {
                webp: '',
                jpg: '',
                default: media.url,
              }
            })
          }
          allowedTypes={['image']}
          value={item[image]?.id}
          render={({ open }) => (
            <div className="repeater-image" style={{ display: 'block', width: '100%' }}>
              {item[image]?.url ? (
                <div className="repeater-image-preview">
                  <img
                    src={item[image].url}
                    alt={item[image].alt || ''}
                    style={{
                      aspectRatio: '16 / 9',
                      width: '100%',
                      height: 'inherit',
                      marginBottom: '8px',
                      objectFit: 'cover',
                      borderRadius: '3px',
                    }}
                  />
                  <div className="repeater-image-controls" style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center', alignItems: 'stretch', width: '100%' }}>
                    <Button onClick={open} variant="secondary" size="small">
                       {__('', 'theme')}
                    </Button>
                    <Button
                      isDestructive
                      onClick={() => update(index, image, { id: 0, url: '', alt: '' })}
                      variant="secondary"
                      size="small"
                    >
                       {__('', 'theme')}
                    </Button>
                  </div>
                </div>
              ) : (
                <Button onClick={open} variant="primary" size="small">
                  {__(' ', 'theme')}
                </Button>
              )}
            </div>
          )}
        />

        <RichText
          style={{ textAlign: 'left' }}
          placeholder={__('', 'theme')}
          value={item[content]}
          onChange={(value) => update(index, content, value)}
        />
      </>
    )
  };

  // ----------------------------
  //    blockTwenty
  // ----------------------------
  const renderBlockTwenty = (item, index) => {
    const title = `title`;
    const sum = `sum`;
    const content = `content`;
    const isButton = 'isButton';

    return (
      <>
        <div style={{ display: 'flex', flex: 'wrap', justifyContent: 'space-between', alignItems: 'stretch', gap: '16px', width: '100%' }}>
          <div style={{ width: '30%' }}>
            <RichText
              style={{ textAlign: 'left' }}
              placeholder={__('', 'theme')}
              value={item[title]}
              onChange={(value) => update(index, title, value)}
            />
            <TextareaControl
              style={{ textAlign: 'left' }}
              placeholder={__('', 'theme')}
              value={item[sum]}
              onChange={(value) => update(index, sum, value)}
              rows={1}
            />
          </div>

          <div style={{
            width: 'calc(70% - 18px)',
            textAlign: 'left',
            minHeight: '100px'
          }}>
            <RichText
              style={{ textAlign: 'left' }}
              placeholder={__('', 'theme')}
              value={item[content]}
              onChange={(value) => update(index, content, value)}
            />
          </div>
        </div>

        <div style={{ width: '100%' }}>
          <ToggleControl
            label={!item[isButton] ? __('  ', 'theme') : __('  ', 'theme')}
            checked={item[isButton]}
            onChange={(value) => update(index, isButton, value)}
          />
        </div>
      </>
    )
  };

  // ----------------------------
  //    TextareaToRichText
  // ----------------------------
  const renderBlockTwentyOne = (item, index) => {
    const title = `title`;
    const content = `content`;

    return (
      <>
        <RichText
          style={{ textAlign: 'left' }}
          placeholder={__('', 'theme')}
          value={item[title]}
          onChange={(value) => update(index, title, value)}
        />
        <RichText
          style={{ textAlign: 'left' }}
          placeholder={__('', 'theme')}
          value={item[content]}
          onChange={(value) => update(index, content, value)}
        />
      </>
    )
  };


  // ----------------------------
  //    blockSeventeen
  // ----------------------------
  const renderBlockTwentyTree = (item, index) => {
    const image = `image`;
    const content = `content`;
    const contacts = `contacts`;

    return (
      <>
        <MediaUpload
          onSelect={(media) =>
            update(index, image, {
              url: media.url,
              alt: media.alt || '',
              responsive: media.responsive || {
                webp: '',
                jpg: '',
                default: media.url,
              }
            })
          }
          allowedTypes={['image']}
          value={item[image]?.id}
          render={({ open }) => (
            <div className="repeater-image" style={{ display: 'block', width: '100%' }}>
              {item[image]?.url ? (
                <div className="repeater-image-preview">
                  <img
                    src={item[image].url}
                    alt={item[image].alt || ''}
                    style={{
                      aspectRatio: '16 / 9',
                      width: '100%',
                      height: 'inherit',
                      marginBottom: '8px',
                      objectFit: 'cover',
                      borderRadius: '3px',
                    }}
                  />
                  <div className="repeater-image-controls" style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'center', alignItems: 'stretch', width: '100%' }}>
                    <Button onClick={open} variant="secondary" size="small">
                       {__('', 'theme')}
                    </Button>
                    <Button
                      isDestructive
                      onClick={() => update(index, image, { id: 0, url: '', alt: '' })}
                      variant="secondary"
                      size="small"
                    >
                       {__('', 'theme')}
                    </Button>
                  </div>
                </div>
              ) : (
                <Button onClick={open} variant="primary" size="small">
                  {__(' ', 'theme')}
                </Button>
              )}
            </div>
          )}
        />

        <RichText
          style={{ textAlign: 'left' }}
          placeholder={__('  ', 'theme')}
          value={item[content]}
          onChange={(value) => update(index, content, value)}
        />

        <RichText
          style={{ textAlign: 'left' }}
          placeholder={__('', 'theme')}
          value={item[contacts]}
          onChange={(value) => update(index, contacts, value)}
        />
      </>
    )
  };

  return {
    list, add, remove, update, moveUp, moveDown, setList,
    renderSelectTextareaControl, renderTextareaToTextarea,
    renderRichTextToRichText, renderBreadcrumbs, renderBlockFourteen,
    renderTextareaToRichText, renderTextRichToRich, renderBlockSeventeen,
    renderBlockNineteen, renderBlockTwenty, renderBlockTwentyOne, renderBlockTwentyTree
  };
}