import { useState } from '@wordpress/element';
import { useBlockProps, RichText, InspectorControls } from '@wordpress/block-editor';
import { Flex, FlexBlock, ToggleControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

import blockImage from '../../../../admin/assets/img/blocks/block-11.jpg';

import CF7FormSelector from '../../components/CF7FormSelector';

import VideoHelpPanel from './controls/VideoHelpPanel';
import ContentPanel from './controls/ContentPanel';
import ImagePanel from "./controls/ImagePanel";

const Edit = ({ attributes, setAttributes }) => {
  const { title, subTitleOne, subTitleTwo, divider, descr } = attributes;

  const [isPreview, setIsPreview] = useState(false);

  const blockProps = useBlockProps({
    className: 'block-style block-11'
  });

  const togglePreview = () => {
    setIsPreview(!isPreview);
  };

  return (
    <>
      <InspectorControls>
        <VideoHelpPanel />
        <ContentPanel attributes={attributes} setAttributes={setAttributes} />
        <ImagePanel attributes={attributes} setAttributes={setAttributes} />
      </InspectorControls>

      <div {...blockProps}>
        <div className="advanced-block">
          <div className="block-info">
            <span className="block-info-title"> Block 11 -    </span>
            <ToggleControl
              label={isPreview ? __(' ', 'theme') : __(' ', 'theme')}
              checked={isPreview}
              onChange={togglePreview}
            />
          </div>

          {!isPreview && (
            <img src={blockImage} alt="MGUBS" style={{ width: '100%', height: 'inherit', objectFit: 'contain' }} />
          )}

          {isPreview && (
            <div className="advanced-block-content">
              <Flex
                direction={[
                  'column',
                  'row'
                ]}
                align="flex-start">
                <FlexBlock>
                  <span>{__('', 'theme')}</span>
                  <RichText
                    tagName="div"
                    value={title}
                    onChange={(value) => setAttributes({ title: value })}
                    placeholder={__('...', 'theme')}
                    allowedFormats={[]}
                  />
                </FlexBlock>
                <FlexBlock>
                  <span>{__(' 1', 'theme')}</span>
                  <RichText
                    tagName="div"
                    value={subTitleOne}
                    onChange={(value) => setAttributes({ subTitleOne: value })}
                    placeholder={__('', 'theme')}
                    allowedFormats={['core/bold', 'core/italic', 'core/link']}
                  />
                </FlexBlock>
              </Flex>

              <ToggleControl
                label={divider ? __('  ', 'theme') : __('  ', 'theme')}
                checked={divider}
                onChange={(value) => setAttributes({ divider: value })}
              />

              <Flex direction={[
                'column',
                'row'
              ]}>
                <FlexBlock>
                  <span>{__(' 2', 'theme')}</span>
                  <RichText
                    tagName="div"
                    value={subTitleTwo}
                    onChange={(value) => setAttributes({ subTitleTwo: value })}
                    placeholder={__('', 'theme')}
                    allowedFormats={['core/bold', 'core/italic', 'core/link']}
                  />
                </FlexBlock>
              </Flex>

              <Flex direction={[
                'column',
                'row'
              ]}>
                <FlexBlock>
                  <span>{__('', 'theme')}</span>
                  <RichText
                    tagName="div"
                    value={descr}
                    onChange={(value) => setAttributes({ descr: value })}
                    placeholder={__('...', 'theme')}
                    allowedFormats={['core/bold', 'core/italic', 'core/link', 'core/underline', 'core/text-color']}
                  />
                </FlexBlock>
              </Flex>

              <Flex direction={[
                'column',
                'row'
              ]}>
                <FlexBlock>
                  <CF7FormSelector attributes={attributes} setAttributes={setAttributes} />
                </FlexBlock>
              </Flex>
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default Edit;