import { useState } from '@wordpress/element';
import {
  useBlockProps, RichText, InspectorControls,
  MediaUpload, MediaUploadCheck
} from '@wordpress/block-editor';
import { Button, ToggleControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

import blockImage from '../../../../admin/assets/img/blocks/block-18.jpg';

import PictureBgEdit from '../../components/PictureBgEdit';

import VideoHelpPanel from './controls/VideoHelpPanel';
import ContentPanel from './controls/ContentPanel';
import BgAnchorPanel from './controls/BgAnchorPanel';

const Edit = ({ attributes, setAttributes }) => {
  const {
    title, descr, btnText, btnLink,
    bg1920Id, bg1920Data, bg991Id, bg991Data, bg576Id, bg576Data
  } = attributes;

  const [isPreview, setIsPreview] = useState(false);

  const togglePreview = () => {
    setIsPreview(!isPreview);
  };

  const bgSizes = [1920, 991, 576];

  // Handler - bg
  const getOnSelectBg = (size) => (media) => {
    setAttributes({
      [`bg${size}Id`]: media.id,
      [`bg${size}Data`]: {
        url: media.url,
        alt: media.alt || '',
        responsive: media.responsive || {
          webp: '',
          jpg: '',
          default: media.url,
        },
      },
    });
  };

  const getOnRemoveBg = (size) => () => {
    setAttributes({
      [`bg${size}Id`]: 0,
      [`bg${size}Data`]: {
        url: '',
        alt: '',
        responsive: {
          webp: '',
          jpg: '',
          default: '',
        },
      },
    });
  };


  const blockProps = useBlockProps({
    className: 'block-style mgu-advantages'
  });

  return (
    <>
      <InspectorControls>
        <VideoHelpPanel />
        <BgAnchorPanel attributes={attributes} setAttributes={setAttributes} />
        <ContentPanel attributes={attributes} setAttributes={setAttributes} />
      </InspectorControls>

      <div {...blockProps}>
        <div className="advanced-block">
          <div className="block-info">
            <span className="block-info-title"> Block 16 - </span>
            <ToggleControl
              label={isPreview ? __(' ', 'theme') : __(' ', 'theme')}
              checked={isPreview}
              onChange={togglePreview}
            />
          </div>

          {!isPreview && (
            <img src={blockImage} alt="MGUBS" style={{ width: '100%', height: 'inherit', objectFit: 'contain' }} />
          )}

          {isPreview && (
            <div className="advanced-block-content">
              <div className="advanced-block-text">
                <>
                  <label htmlFor="rich-title" className="my-rich-text__label"></label>
                  <RichText
                    id="rich-title"
                    tagName="div"
                    label=""
                    value={title}
                    onChange={(value) => setAttributes({ title: value })}
                    placeholder={__('...', 'theme')}
                    allowedFormats={[]}
                  />
                </>

                <>
                  <label htmlFor="rich-description" className="my-rich-text__label"></label>
                  <RichText
                    id="rich-description"
                    tagName="div"
                    value={descr}
                    onChange={(value) => setAttributes({ descr: value })}
                    placeholder={__('...', 'theme')}
                    allowedFormats={['core/bold', 'core/italic', 'core/link']}
                  />
                </>

                <>
                  <label htmlFor="rich-description" className="my-rich-text__label">: </label>
                  <RichText
                    id="rich-description"
                    tagName="div"
                    value={btnText}
                    onChange={(value) => setAttributes({ btnText: value })}
                    placeholder={__('...', 'theme')}
                    allowedFormats={[]}
                  />
                </>

                <>
                  <label htmlFor="rich-description" className="my-rich-text__label">: </label>
                  <RichText
                    id="rich-description"
                    tagName="div"
                    value={btnLink}
                    onChange={(value) => setAttributes({ btnLink: value })}
                    placeholder={__('...', 'theme')}
                    allowedFormats={[]}
                  />
                </>
              </div>

              {bgSizes.map((size) => (
                <PictureBgEdit
                  key={size}
                  label={` ${size}px`}
                  imageId={attributes[`bg${size}Id`]}
                  imageData={attributes[`bg${size}Data`]}
                  onSelect={getOnSelectBg(size)}
                  onRemove={getOnRemoveBg(size)}
                />
              ))}
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default Edit;